.footer {
    display: flex;
    justify-content: center;
    position: relative;

    .video {
        position: absolute;
        width: 100vw;
        height: 100vh;
    }

    .container {
        height: 100vh;
        justify-content: space-evenly;

        .logo {
            color: #fff;
            margin: 0;
            align-self: start;
        }

        .talk-to-us {
            display: flex;
            flex-direction: column;
            align-self: center;

            h1 {
                color: var(--themer-content);

                span {
                    color: var(--themer-heading);
                }
            }

            button {
                align-self: flex-end;
                margin-top: 30px;
            }
        }

        .footer-bar {
            width: 100%;
            display: flex;

            p {
                margin-bottom: 0;
            }

            .contact {
                * {
                    color: #5B5B5B;
                    text-align: left;
                }

                >* {

                    margin: 8px;
                }

                margin-bottom: 0;

                display: flex;
                justify-content: space-between;
                align-items: center;
                width: 100%;

                .item {
                    font-size: 13px;
                    padding: 0;
                    margin: 0;
                    .link a{
                        
                        text-decoration: none !important;
                    }
                    .link :hover {

                        color: var(--themer-accent);
                    }
                }

                .item .highlight {
                    color: #999999;
                }

            }

            .social {
                >* {

                    margin-left: 24px;
                }

                display: flex;
                width: 30%;
                flex: 1;

                img {
                    height: 30px;
                    width: 30px;
                }
            }

            .social :first-child {
                margin-left: 56px;
            }
        }

        .developer-text {
            position: absolute;
            bottom: 24px;

            a {
                color: var(--themer-accent);
            }

            span {
                color: var(--themer-heading);
            }

            justify-self: flex-end;
        }
    }
}

.page.footer {
    padding-bottom: 0;
}

@media only screen and (max-width:480px) {
    .footer {
        .container {
            .logo {
                align-self: start;
            }

            .talk-to-us {

                h1 {
                    font-size: 32px;
                    color: var(--themer-content);

                    span {
                        color: var(--themer-heading);
                    }
                }

                button {
                    align-self: flex-start;
                    margin-top: 18px;
                }
            }

            .footer-bar {
                $margin: 8px;

                .contact {
                    flex-direction: column;
                    align-items: flex-start;

                    .item {
                        margin-bottom: $margin;
                    }

                    .item .highlight {
                        color: var(--themer-accent);
                    }
                }

                .social {
                    display: flex;
                    flex-direction: column;
                    flex: 1;

                    img {
                        margin-bottom: 24px;
                        margin-top: 10px;
                        margin-left: 0px;
                        height: 30px;
                        width: 30px;
                    }
                }

                .social :first-child {
                    margin-left: 0px;
                }
            }

        }
    }
}