.details-section {
    align-self: center;
    width: 75%;
    margin: 15% 0;

    h2 {
        font-size: 44px;
        color: var(--themer-heading);
        margin-bottom: 34px;
    }

    p {
        font-size: 28px;
        line-height: 37px;
        margin-bottom: 60px;
    }

    .items {
        text-transform: uppercase;
        margin-bottom: 15px;
        width: 30%;
        color: var(--themer-content);

        span {
            font-size: 9px;
            color: var(--themer-content);
        }

        span:hover {
            color: var(--themer-accent);
        }
    }

}

@media only screen and (max-width:480px) {
    .details-section {
        width: 100%;
        margin: 30% 0;

        h2 {
            font-size: 30px;
            margin-bottom: 34px;
        }

        p {
            font-size: 21px;
            line-height: 28px;
        }

        .items {
            text-transform: uppercase;
            margin-bottom: 20px;
            width: 100%;
            color: var(--themer-content);

            .item-inner {
                display: flex;
            }

            .bullet {
                align-self: center;
                $bullet-size: 10px;
                width: $bullet-size;
                height: $bullet-size;
                margin-right: 20px;
                background-color: var(--themer-content);
            }

            span {
                width: 100%;
                font-size: 12px;

            }
        }

    }
}